import store from '@/store'
import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: () => import('@/views/HomeView.vue'),
    meta: { requiresAuth: true }
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import('@/views/auth/LoginView.vue'),
    meta: { guest: true }
  },
  {
    path: '/register',
    name: 'Register',
    component: () => import('@/views/auth/RegisterView.vue'),
    meta: { guest: true }
  },
  // --- ROUTE ADMIN ---
  {
    path: '/admin',
    name: 'Admin',
    component: () => import('@/views/admin/AdminView.vue'),
    meta: { requiresAuth: true, role: 'admin' }
  },
  // Routes Enseignant
  {
    path: '/teacher/session',
    name: 'TeacherSession',
    component: () => import('@/views/teacher/SessionView.vue'),
    meta: { requiresAuth: true, role: 'teacher' }
  },
  {
    path: '/teacher/attendances',
    name: 'TeacherAttendances',
    component: () => import('@/views/teacher/AttendanceView.vue'),
    meta: { requiresAuth: true, role: 'teacher' }
  },
  // Routes Étudiant
  {
    path: '/student/scan',
    name: 'StudentScan',
    component: () => import('@/views/student/ScanView.vue'),
    meta: { requiresAuth: true, role: 'student' }
  },
  {
    path: '/student/validation',
    name: 'StudentValidation',
    component: () => import('@/views/student/PeerValidationDialog.vue'),
    meta: { requiresAuth: true, role: 'student' }
  },
  // Routes communes
  {
    path: '/profile',
    name: 'Profile',
    component: () => import('@/views/ProfileView.vue'),
    meta: { requiresAuth: true }
  },
  {
    path: '/about',
    name: 'About',
    component: () => import('@/views/AboutView.vue')
  },
  // Catch-all
  {
    path: '*',
    redirect: '/'
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  scrollBehavior() {
    return { x: 0, y: 0 }
  }
})

let userFetched = false

router.beforeEach(async (to, from, next) => {
  const isAuthenticated = store.getters['auth/isAuthenticated']
  let userRole        = store.getters['auth/userRole']

  // Charger l'utilisateur une seule fois
  if (isAuthenticated && !userRole && !userFetched) {
    userFetched = true
    await store.dispatch('auth/fetchUser')
    userRole = store.getters['auth/userRole']
  }

  // Si la route exige une auth
  if (to.matched.some(r => r.meta.requiresAuth)) {
    if (!isAuthenticated) {
      return next({ name: 'Login', query: { redirect: to.fullPath } })
    }

    // Si on va sur la Home, on redirige selon le rôle
    if (to.name === 'Home') {
      const dashboard =
          userRole === 'admin'   ? '/admin' :
              userRole === 'teacher' ? '/teacher/session' :
                  '/student/scan'
      return next(dashboard)
    }

    // Si on a une route “role” et qu’on n’a pas le bon rôle
    if (to.meta.role && userRole !== to.meta.role) {
      const redirectPath =
          userRole === 'admin'   ? '/admin' :
              userRole === 'teacher' ? '/teacher/session' :
                  '/student/scan'
      return next(redirectPath)
    }

    // OK, on reste
    return next()
  }

  // Si la route est pour les invités et qu’on est déjà connecté
  if (to.matched.some(r => r.meta.guest) && isAuthenticated) {
    return next('/')
  }

  // Sinon, on laisse passer
  next()
})

// ------------------------------------------------------------------
// IGNORER les “Redirected when going from …” qui plantent la console
// ------------------------------------------------------------------
Vue.config.errorHandler = (err) => {
  if (
      err &&
      err.message &&
      err.message.startsWith('Redirected when going from')
  ) {
    // on ignore purement et simplement
    return
  }
  // pour toute autre erreur, on la logue
  console.error(err)
}

export default router
